.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.card {
  background: rgb(37,45,55);
background: linear-gradient(41deg, rgba(37,45,55,1) 32%, rgba(68,77,90,1) 100%);
  width: 500px;
  height: 500px;
  padding: 30px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.star {
  background-color: hsl(213, 19%, 25%);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.star img{
  width: 50%;
}

.card h1{
  color: white;
}

.card p{
  font-size: 18px;
  color: rgba(255, 255, 255, 0.436);
  max-width: 400px;
  margin-bottom: 20px;
}

.card .btns{
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 20px;
}

.card .btns button{
  all: unset;
  background-color: hsl(213, 19%, 25%);
  color: rgba(255, 255, 255, 0.436);
  font-weight: 700;
  font-size: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: .3s;
}

.card .btns button:hover{
  background-color: hsl(25, 97%, 53%);
  color: white;
}

.card .btns button:focus{
  background-color: hsl(217, 12%, 63%);
  color: white;
}

.submit{
  all: unset;
  background-color: hsl(25, 97%, 53%);
  height: 50px;
  text-align: center;
  border-radius: 30px;
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  cursor: pointer;
  transition: .3s;
}

.submit:hover{
  background-color: white;
  color: hsl(25, 97%, 53%);
}

.thanks-img{
  width: 200px;
}

.card2{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding: 50px 30px;
}

.card2 .rating{
  background-color: hsl(213, 19%, 25%);
  padding: 5px 20px;
  font-size: 18px;
  border-radius: 30px;
  color: hsl(25, 97%, 53%);
}

.desc{
  text-align: center;
}